  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #F5F6F4;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}
.button--logo:hover::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

.menu {
