}
.menu__checkbox {
  position: absolute;
  left: -9999px;
}
@media screen and (max-width: 768px) {
  .menu__checkbox:checked ~ .menu__list {
    display: flex;
    flex-direction: column;
    text-align: center;
  }
  .menu__checkbox:checked ~ .menu__toggler {
    rotate: -90deg;
    transform-origin: center;
  }
}
