.menu__item {
  font-family: "Jomhuria", serif;
  color: #F5F6F4;
  padding: 20px;
  z-index: 1;
  position: relative;
  border-bottom: 1px solid #F5F6F4;
  transition: transform 0.95s ease-out;
}
.menu__item:last-child {
  border: none;
}
.menu__item--dark {
  color: #685B5E;
  color: #F5F6F4;
}
@media screen and (min-width: 768px) {
  .menu__item--dark {
    color: #685B5E;
  }
}
@media screen and (max-width: 768px) {
  .menu__item:hover {
    background-color: #F5F6F4;
    color: #685B5E;
  }
}
@media screen and (min-width: 768px) {
  .menu__item {
    padding: 40px 20px;
    border: none;
  }
  .menu__item::before {
    content: "";
    position: absolute;
    width: 70%;
    transform: scaleX(0);
    height: 8px;
    bottom: 48px;
    left: 15%;
    background-color: #BB8B9E;
    transform-origin: bottom right;
    transition: transform 0.25s ease-out;
