    margin: 80px auto;
  }
}
.toggle__container--nabidka {
  margin-bottom: 0;
}
.toggle__text {
  font-family: "Jomhuria", serif;
  color: #B3A8A8;
  font-size: 30px;
  padding: 0 40px;
  cursor: pointer;
}
.toggle__text--psi {
  color: #685B5E;
}
.toggle__switch {
  position: relative;
  width: 200px;
  height: 8px;
  background-color: #D9D9D9;
  border-radius: 5px;
}
.toggle__checkbox {
  display: none;
}
.toggle__checkbox:checked + .toggle__label .toggle__slider {
  left: calc(100% - 55px);
  transition: 1s;
}
.toggle__slider {
  position: absolute;
  top: 5px;
  left: 60px;
  transform: translate(-50%, -50%);
  transition: left 1s;
  cursor: pointer;
}

.reveal {
  position: relative;
  transform: translateY(100px);
  opacity: 0;
  transition: 0.5s all ease;
}
.reveal--left {
  transform: translateX(-100px);
}
.reveal--right {
  transform: translateX(100px);
}
.reveal--opacity {
  transform: translateY(0);
  transition: 2s all ease;
}
@media screen and (min-width: 768px) {
  .reveal--phone {
    opacity: 1;
    transform: translateY(0);
  }
}
.reveal.active {
  transform: translateY(0);
  opacity: 1;
}
.reveal.active.reveal--left {
  transform: translateX(0);
}
.reveal.active.reveal--right {
  transform: translateX(0);
}

